import { Component, Prop, h, State, Listen, Event, EventEmitter, Element } from '@stencil/core';
import { NavigationItem } from '../../types/header-link';
/**
 * The `gsl-shell` component serves as the layout wrapper for the application. It combines the header,
 * side navigation, and content area, and manages shared state such as the locked and expanded
 * state of the side nav. It listens for toggle events and persists the lock state using localStorage.
 *
 * @slot header-center - Injects content into the center slot of the header
 * @slot header-right - Injects actions or elements into the right side of the header
 * @slot default - Main content area below the header and beside the nav
 * @event gslToggleSidenav - Emits when the side nav is toggled by header or nav
 * @event gslToggleLock - Emits when the lock state is toggled from the side nav
 */
@Component({
  tag: 'gsl-shell',
  styleUrl: 'gsl-shell.scss',
  shadow: true,
})
export class GslShell {
  @Element() el: HTMLElement;

  /** Navigation structure passed into the side nav component */
  @Prop() navigationData: NavigationItem[] = [];
  /** The currently active path, used to highlight the selected nav item */
  @Prop() currentPath: string;
  /** Title displayed in the header */
  @Prop() pageTitle: string;
  /** Path to the logo image displayed in the header */
  @Prop() logoSrc: string;
  /** alt text for the default logo */
  @Prop() logoAlt: string = 'Goddard School logo';
  /** URL the logo should link to */
  @Prop() logoHref: string;
  /** Whether the side nav is currently expanded */
  @State() isExpanded: boolean = false;
  /** Whether the side nav is locked open; persisted in localStorage */
  @State() locked: boolean = false;
  /** Event emitted when side nav is toggled open or closed */
  @Event() gslToggleSidenav: EventEmitter<{ expanded: boolean, source: 'header' | 'side-nav' }>;
  /** Event emitted when the side nav lock state changes */
  @Event() gslToggleLock: EventEmitter<{ locked: boolean, source: 'header' | 'side-nav' }>;

  componentWillLoad() {
    const storedLock = localStorage.getItem('gsl-shell-locked');
    if (storedLock !== null) {
      if (storedLock === 'true') {
        this.locked = true;
        this.isExpanded = true;
      }

    }
  }

  @Listen('gsl:toggle-side-nav')
  handleHeaderToggle() {
    this.isExpanded = !this.isExpanded;
    this.gslToggleSidenav.emit({ expanded: this.isExpanded, source: 'header' });
    if (!this.isExpanded) {
      this.handleForceUnlock();
    }
  }

  @Listen('gsl:toggle-lock')
  handleLockToggle() {
    this.locked = !this.locked;
    localStorage.setItem('gsl-shell-locked', this.locked.toString());
    this.gslToggleLock.emit({ locked: this.locked, source: 'side-nav' });
  }

  @Listen('gsl:toggle-expand')
  handleSideNavExpand() {
    this.isExpanded = true;
    this.gslToggleSidenav.emit({ expanded: true, source: 'side-nav' });
  }

  @Listen('gsl:force-unlock')
  handleForceUnlock() {
    this.locked = false;
    localStorage.setItem('gsl-shell-locked', 'false');
  }

  render() {

    return (
      <div class="shell">
        <header class="header">
          <gsl-header
            pageTitle={this.pageTitle}
            logoSrc={this.logoSrc}
            navExpanded={this.isExpanded}
            logoHref={this.logoHref}
            logoAlt={this.logoAlt}
          >
            <slot name="header-center" slot="center" />
            <slot name="header-right" slot="right-actions" />
          </gsl-header>
        </header>
        <div class="main">
          <gsl-side-nav
            navigationData={this.navigationData}
            currentPath={this.currentPath}
            locked={this.locked}
            isExpanded={this.isExpanded}
          />
          <div class="content-area">
            <main>
              <slot />
            </main>
          </div>
        </div>
      </div>
    );
  }
}
